
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
export default {
components: {
Group,
Cell,
Toast
},
data () {
return {
avartar: 'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132',
password:'',
params: {
'sessionId': '',
'carType': '',
'type':''
},
showPositionValue: false,
middle: 'middle',
msg:'msg',
stuMsg: {}
}
},
created () {
this.params.sessionId = getCookie('sessionId')
this.params.carType = this.$route.query.carType || null
this.params.type = this.$route.query.type || null


this.toExam()
},
methods: {
toExam () {
const that = this;
Drive.prototype.toExam(that.params).then(res => {
let list = res.data.data || [];
console.log(list)
that.stuMsg = list
});
},
onBindTap (){

const that = this
that.$router.push('../question?mid='+ this.stuMsg.mid + '&&type=' + this.params.type + '&&carType=' + this.params.carType)
}
}
}



